.photo_page {
    width: 100%;
    height: 100%;
    padding-top: 50px;
    font-family: "nanumpen";
    display: grid;
    grid-template-columns: 30% 30% 30%;
    grid-template-rows: 25% 25% 25%;
    justify-items: center;
    align-items: center;
    justify-content: space-evenly;
    gap: 5px 5px;
    align-content: center;

    a {

        width: 100%;
        height: 100%;

        .photo_img{
            width: 100%;
            height: 100%;
            padding-top: 5px;
            padding-bottom: 5px;
            object-fit: cover;
        }
    }
}